import React, { useRef ,useState, useEffect } from 'react';
import ScrollArea from 'react-scrollbar'; //stricMode context API error
import {listData} from './listdata';

const History = ({headerSub, isWork, workState}) => {
  const workBox = useRef(null);
  const [workHeight, setHeight] = useState(0);

  const handleResize = () =>{
    setHeight(workBox.current.offsetHeight);
  }


  useEffect(() => {
    headerSub ();

    if(workBox.current){
      setHeight(workBox.current.offsetHeight);
      window.addEventListener("resize", handleResize);
    }
    workState(true);
    return () => window.removeEventListener('resize', handleResize);

  }, [headerSub, workBox, isWork, workState]);


  return (
    <div className={"work" + (isWork ? ' isWork' : '')} ref={workBox}>
      <ScrollArea
        speed={0.3}
        style={{height: workHeight + 'px'}}
        vertical={true}
        smoothScrolling={true}
        >
        <div className="list">
          <ul>
            {listData.map((site, idx) => (
            <li key={idx}>
              <a href={site.link}>
                <figure>
                  <i>{site.year}</i>
                  <img src={site.thum} alt="" />
                </figure>
                <dl>
                  <dt>{site.title}</dt>
                  <dd className="text">
                    {site.desciption.map((text, i) => (
                      <span key={i}>{text}</span>
                    ))}
                  </dd>
                  <dd className="tools">
                    {site.tools}
                  </dd>
                </dl>
              </a>
            </li>
            ))}
          </ul>
        </div>
      </ScrollArea>
    </div>
  )
};
export default History;