import React, { useState, useRef, useEffect } from 'react';
import {Route, Switch, Redirect , useHistory } from 'react-router-dom';
import Header from "./components/header";
import Main from "./components/Main/index";
import Work from "./components/Work/index";

const App = () => {
  const [isMainNav, setMainNav] = useState(false);
  const [isMain, setMain] = useState(false);
  const [isSub, setSub] = useState(false);
  const [isWork, setWork] = useState(false);
	const wave = useRef(null);
  let isPlaying = false;
  let { current } = wave;

  const headerSub = () => {
    setMain(false);
    setSub(true);
    setMainNav(true);
  }

  const mainClosing = () => {
    setMain(false);
    current.style.transform = 'translateY(0px)';
  }

  const workState = (e) => {
    setWork(e);
  }

	const videoPlay = (e) => {
		if(isPlaying){
			return;
		}
		setMainNav(false);
		e.target.play();
		isPlaying = true;

		e.target.addEventListener('timeupdate', (e) => {
			let currentTime = e.target.currentTime;
			if(e.target.ended){
				isPlaying = false;
			}
			if(currentTime > 6){
				setMainNav(true);
				return;
			}
			current.style.transform = 'translateY(-' + (currentTime * 50) +'px)';
			//console.log(e.target.currentTime);
		});

	}
  const history = useHistory();

  // history 액션에서도 state 를 변경해주어 page active 를 실행함.
  useEffect(() => {
    setMain(true);
    return history.listen((location) => {
      if(location.pathname === '/'){
        setTimeout(()=>{
          setMain(true);
        },100);
        setMainNav(false);
        setSub(false);
        setWork(false);
        
      } else {
        setMain(false);
        setSub(true);

        if(location.pathname === '/work'){
          setTimeout(()=>{
            setWork(true);
          },300);
        }
      }
    })
  },[history])


	return (
    <>
      <Header 
        isMainNav={isMainNav} 
        isMain={isMain} 
        isSub={isSub} 
        headerSub={headerSub}
        mainClosing={mainClosing}
        workState={workState}
        isWork={isWork}
      /> 
      <Switch>
        <Route exact path="/" render={() => 
          <Main 
            isMainNav={isMainNav} 
            videoPlay={videoPlay} 
            isMain={isMain} 
            wave={wave} 
            isWork={isWork}
            />} 
          />
        <Route exact path="/work" render={() => <Work headerSub={headerSub} isWork={isWork} workState={workState} />} />
        <Redirect path="*" to="/" />
      </Switch>
    </>
	);
}

export default App;